    @import url('https://fonts.googleapis.com/css2?family=Bangers&family=Inter:wght@200;400;500;700&family=Special+Elite&display=swap');
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    
    body {
        font-family: 'Bangers', sans-serif;
        font-weight: 400;
        font-size: 100%;
        overflow-x: hidden;
        word-wrap: break-word;
        background: url(../img/papel.webp) 100% 100% repeat;
    }
    
    h1 {
        font-size: 1.8rem;
        line-height: 1.2;
        font-weight: 800;
        margin-top: 0;
        margin-bottom: 0;
        text-align: center;
    }
    
    h2 {
        font-size: 1.5rem;
        line-height: 1em;
        font-weight: 800;
        padding: 20px;
        text-align: center;
    }
    
    h3 {
        font-size: 1.25rem;
        margin-bottom: 1rem;
        font-weight: 600;
    }
    
    p {
        line-height: 1.2;
        margin-bottom: 1rem;
        font-family: 'Special Elite', cursive;
    }
    
    a {
        font-size: .8rem;
        margin-top: 5px;
        text-decoration: none;
        color: black;
    }
    
    a:hover {
        color: red
    }
    
    figcaption {
        text-align: center;
        padding: 15px;
    }
    
    @media (min-width: 1024px) {
        h1 {
            font-size: 6rem;
        }
        h2 {
            font-size: 2.5rem;
            text-align: center;
        }
        h3 {
            font-size: 1.5rem;
        }
        p {
            font-size: 1.6rem;
            padding-left: 30px;
            padding-right: 30px;
        }
    }
    /****************************
      Contenedores del sitio
     *****************************/
    
    div {
        background: url(../img/papelclaro.webp) 100% 100% repeat;
    }
    
    main {
        width: 90%;
        margin: auto;
    }
    
    header {
        padding: 10px 0 10px 0;
        width: 90%;
    }
    
    footer {
        padding: 30px;
        text-align: center;
        width: 90%;
        margin: auto;
    }
    
    .container,
    .video,
    section {
        margin-right: auto;
        margin-left: auto;
        padding: 10px;
    }
    
    section {
        padding-bottom: 20px;
    }
    
    @media (min-width: 1024px) {
        .titulopagina {
            padding: 0;
            margin: 0;
            width: 80%;
            margin: auto;
            text-align: center;
        }
        section {
            padding-bottom: 5px;
        }
    }
    /****************************
         Contenidos del header
        *****************************/
    
    header {
        text-align: center;
    }
    
    .foto {
        flex: 1;
        padding-right: 0px;
    }
    
    .ibaÃ±ez {
        width: auto;
        height: 100px;
        border-radius: 50%;
        box-shadow: 0 0 0 3px black;
    }
    
    .cabecera {
        flex: 3;
        padding: 0;
        margin: 0;
        text-align: center;
        flex: 1;
    }
    
    @media (min-width: 1024px) {
        header {
            display: flex;
        }
        .foto {
            flex: 1;
            padding-right: 0px;
        }
        .cabecera {
            flex: 4;
            padding-right: 0px;
        }
