    /****************************
         Contenidos pÃ¡gina principal
        *****************************/
    
    .logo {
        height: 150;
        width: 84px;
    }
    
    svg {
        animation: mortadelo 3s infinite ease-in;
        width: 100%;
    }
    
    @keyframes mortadelo {
        from {
            transform: translate(0%, 0);
        }
        to {
            transform: translate(90vw, 0);
        }
    }
    
    .indexx .container p {
        padding: 0;
    }
    
    .img-index {
        height: 170px;
        width: 270px;
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-top: 15px;
        margin-bottom: 15px;
        transition: width 2s, height 2s
    }
    
    .img-index:hover {
        width: 50px;
        height: 50px;
    }
    
    .index {
        width: 100%;
        height: auto;
        padding: 10px;
    }
    
    .indexx .container {
        height: auto;
        width: 100%;
        box-shadow: 0 0 0 5px white;
        margin-top: 30px;
        margin-bottom: 30px;
    }
    
    .indexx {
        height: auto;
        width: 100%;
        padding-bottom: 30px;
    }
    
    @media (min-width: 1024px) {
        .img-index {
            height: 200px;
            width: 350px;
        }
        svg {
            animation-duration: 6s;
        }
        .indexx .container {
            display: flex;
        }
        .indexx {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
            grid-template-rows: 1fr 1fr 1fr;
        }
        .indexx .container:nth-child(1) {
            grid-column-start: 2;
            grid-column-end: 5;
            grid-row: 1;
        }
        .indexx .container:nth-child(2) {
            grid-column-start: 3;
            grid-column-end: 6;
            grid-row: 2;
        }
        .indexx .container:nth-child(3) {
            grid-column-start: 2;
            grid-column-end: 5;
            grid-row: 3;
        }
    }
    /****************************
      MenÃº de navegaciÃ³n del sitio
 *****************************/
    
    nav {
        width: 90%;
        margin: auto;
    }
    
    .menu>ul>li {
        box-shadow: 0 0 0 2px black;
        flex: 1;
        margin: 10px;
        justify-content: space-between;
        text-align: center;
        line-height: 30px;
        background: url(../img/papelclaro.webp) 100% 100% repeat;
        font-family: 'Special Elite', cursive;
    }
    
    .menu>ul>li>a {
        text-decoration: none;
        color: black;
    }
    
    .menu>ul>li>a:hover {
        color: red;
    }
    
    nav li.active a {
        font-weight: 800;
        color: red;
    }
    
    .menu>ul {
        padding: 0px;
        display: flex;
        list-style: none;
    }
    
    .menufooter {
        list-style: none;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    
    .menufooter li {
        text-align: center;
        font-family: 'Special Elite', cursive;
    }
    
    .menufooter li a {
        font-weight: 600;
        line-height: 40px;
        font-size: 1.5rem;
    }
    
    @media (min-width: 1024px) {
        nav {
            width: 50%;
            margin: auto;
            padding-bottom: 50px;
        }
        nav ul {
            display: flex;
        }
        nav ul li {
            text-align: center;
            margin-right: 30px;
            font-family: 'Special Elite', cursive;
            font-weight: 600;
            padding: 5px;
        }
        nav ul li:last-child {
            margin-right: 0;
        }
        nav ul li a,
        .menufooter li a {
            font-size: 1.5rem;
        }
        .menufooter {
            margin: auto;
            width: 50%;
            flex-direction: row;
        }
        .menufooter li {
            box-shadow: 0 0 0 2px black;
        }
    }
    /****************************
  ENLACES DE PÃGINA
    *****************************/
    
    .leermas {
        color: red
    }
    /****************************
      Contenido Lista PERSONAJES
 *****************************/
    
    .figPortada {
        display: none;
        transition: all .5s;
    }
    
    .figPortada:hover>figcaption {
        display: block;
        transition: all .5s;
    }
    
    .personaje {
        margin-bottom: 20px;
    }
    
    .portada {
        height: 268px;
        width: 188px;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    
    @media (min-width: 1024px) {
        .personajes {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr 1fr 1fr;
            background-image: none;
        }
        .personaje {
            display: flex;
        }
    }
    
    @media (orientation:landscape) {
        .personajes {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr 1fr 1fr;
            background-image: none;
        }
    }
    /****************************
      Contenido detalle PERSONAJES
 *****************************/
    
    .portadaPer {
        height: 268px;
        width: 188px;
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-top: 15px;
        margin-bottom: 15px;
    }
    
    .fotosp {
        height: 220px;
        width: 98%;
        display: block;
        margin: auto;
        animation: Circulo 4s infinite;
    }
    
    @keyframes Circulo {
        0% {
            clip-path: circle(12.3% at 50% 50%);
        }
        25% {
            clip-path: circle(31.6% at 50% 50%);
        }
        50% {
            clip-path: circle(45.4% at 50% 50%);
        }
        75% {
            clip-path: circle(57.6% at 50% 50%);
        }
        100% {
            clip-path: circle(70.7% at 50% 50%);
        }
    }
    
    article {
        margin-bottom: 30px;
        margin: auto;
    }
    
    .fotosPersonajes1 .container {
        width: 100%;
        border-color: burlywood;
        border-width: 4px;
        border-style: solid;
    }
    
    figure p {
        font-family: 'Bangers', sans-serif;
        background-color: white;
        size: 3rem;
        margin-bottom: 0;
        width: 98%;
        display: block;
        margin: auto;
        text-align: center;
        border-color: black;
        border-width: 1px;
        border-style: solid;
    }
    
    @media (min-width: 768px) and (max-width: 1024px) {
        .fotosPersonajes1 figure {
            margin: auto;
            width: 60%;
        }
    }
    
    @media (min-width: 1024px) {
        iframe {
            width: 560px;
            height: 315px;
        }
        .fotosp {
            height: 260px;
            width: 300px;
        }
        figure p {
            width: 300px;
        }
        .video figure p {
            width: 70%;
        }
        .fotosPersonajes1 {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-template-rows: .25fr 1fr 1fr;
            background-image: none;
        }
        .sacarino .fotosPersonajes1,
        .rompetechos .fotosPersonajes1,
        .mortadelo .fotosPersonajes1 {
            grid-template-columns: 1fr 1fr 1fr;
            grid-template-rows: .25fr 1fr;
        }
        .rompetechos .fotosPersonajes1,
        .pepe .fotosPersonajes1 {
            grid-template-columns: 1fr 1fr;
            grid-template-rows: .15fr 1fr;
        }
        .fotosPersonajes1 :nth-child(1) {
            grid-column-start: 1;
            grid-column-end: 4;
            grid-row-start: 1;
        }
        .fotosPersonajes1 :nth-child(2) {
            grid-column-start: 1;
            grid-column-end: 1;
            grid-row-start: 2;
        }
        .fotosPersonajes1 :nth-child(3) {
            grid-column-start: 2;
            grid-column-end: 2;
            grid-row-start: 2;
        }
        .fotosPersonajes1 :nth-child(4) {
            grid-column-start: 3;
            grid-column-end: 3;
            grid-row-start: 2;
        }
        .fotosPersonajes1 :nth-child(5) {
            grid-column-start: 1;
            grid-column-end: 1;
            grid-row-start: 3;
        }
        .fotosPersonajes1 :nth-child(6) {
            grid-column-start: 2;
            grid-column-end: 2;
            grid-row-start: 3;
        }
        .fotosPersonajes1 :nth-child(7) {
            grid-column-start: 3;
            grid-column-end: 3;
            grid-row-start: 3;
        }
        .portadaPer {
            width: auto;
        }
    }
    /****************************
       Contenido BIOGRAFIA
     *****************************/
    
    .bio header {
        text-align: center;
        background: none;
        color: black;
        margin-bottom: 30px;
        margin: auto;
    }
    
    .fotosb {
        height: 220px;
        width: 98%;
        margin: auto;
        display: block;
        margin: auto;
    }
    
    .fotosb1 {
        margin: auto;
        display: block;
        animation: bio 5s infinite;
    }
    
    @keyframes bio {
        0% {
            clip-path: polygon(83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%, 50% 0%);
        }
        10% {
            clip-path: polygon( 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%, 50% 0%, 83% 12%);
        }
        20% {
            clip-path: polygon( 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%, 50% 0%, 83% 12%, 100% 43%);
        }
        30% {
            clip-path: polygon( 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%, 50% 0%, 83% 12%, 100% 43%, 94% 78%);
        }
        40% {
            clip-path: polygon( 6% 78%, 0% 43%, 17% 12%, 50% 0%, 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%);
        }
        50% {
            clip-path: polygon( 0% 43%, 17% 12%, 50% 0%, 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%);
        }
        60% {
            clip-path: polygon( 17% 12%, 50% 0%, 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%);
        }
        70% {
            clip-path: polygon( 50% 0%, 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%);
        }
        80% {
            clip-path: polygon( 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%, 50% 0%);
        }
        90% {
            clip-path: polygon( 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%, 50% 0%, 83% 12%);
        }
        100% {
            clip-path: polygon(94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%, 50% 0%, 83% 12%, 100% 43%);
        }
    }
    
    @media (min-width: 1024px) {
        .fotosb {
            height: 350px;
            width: 600px;
            margin: auto;
        }
        .bio {
            padding: 20px;
        }
        .bio h2 {
            text-align: left;
        }
    }
    
    @media(max-width: 992px) and (orientation:landscape) {
        .fotosb {
            height: 220px;
            width: 70%;
        }
    }
